import React, { useEffect, useState } from "react"
import "./ProductShowcase.scss"

import parse from "html-react-parser"

import { useStaticQuery, graphql, Link } from "gatsby"
import { useQueryParam, NumberParam } from "use-query-params"
import { siteRoutes } from "./../utils/siteRoutes"
import globalUtils from "../../global-utils"
import { productsQuery } from "../utils/utils"
import ProductItem from "./ProductItem"
import emitter from "./../../.cache/emitter"

export default function ProductShowcase(props) {
  const [products, setProducts] = useState([])

  let data = productsQuery()

  useEffect(() => {
    setProducts(globalUtils.sanitizeProducts(data))
  }, [])

  return (
    <section className="ProductShowcase">
      <div className="container">
        <div className="row justify-content-center">
          <div className="col-12 col-md-10 col-lg-7">
            {/* Section Heading */}
            <div className="section-heading text-center">
              <h2 className="text-capitalize">Blog</h2>
              <p className="d-sm-block mt-4">
                Check our latest tips, tricks, articles and insights for the
                Shopify realm.
              </p>
            </div>
          </div>
        </div>
        <div className="row">
          {[...products].map(e => {
            return <ProductItem item={e}></ProductItem>
          })}
        </div>
        <div className="row">
          <div className="col-12">
            <Link className="btn" to={siteRoutes.blog}>
              See all products
            </Link>
          </div>
        </div>
      </div>
    </section>
  )
}
